



.Login {
    text-align: left;
    padding: 2rem;
    margin: auto;
    border: none;
    border-radius: 14px;
    vertical-align: middle;
    margin-left: auto;
    margin-right: auto;
    width: 1000px;
    height: 500px;
    background: #fff;
}

.Login h2 {
    font-size: 18px;
    margin-bottom: 30px;
    font-weight: 700;
}

label {
  display: flex;
  font-weight: 400;
}

.btn:hover{color: #fff;}

.submit-btn-login{
    background: #449dad !important;
    box-shadow: none;
    border: none;
    border-radius: 60px;
    text-transform: none;
    padding: 0.375rem 1.8rem;
    color: #fff;
    margin-top: 4px;
  }
.submit-btn:hover,
.submit-btn:focus, 
.submit-btn:active, 
.submit-btn.active,
button.submit-btn.btn.btn-secondary:not([disabled]):not(.disabled):active{background: #398a99 !important;
    box-shadow: none;
    border: none;
    text-transform: none;
    border-radius: 60px;

    padding: 0.375rem 1.8rem;
    
    color: #fff;}


.form-control {
    font-size: 13px;
    height: 40px;
    box-shadow: none;
}
.Login-title {
  font-size: 1.5em;
}

.Login-intro {
  font-size: large;
}

@keyframes Login-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.login-page-header img{height: 45px;}
.login-page {
    display: flex;
    align-items: center;
    height: 100vh;
    background-size: cover;
}
.login-page-header {
    align-items: center;
    background: #fff;
    text-align: center;
    padding: 14px 30px;
    border-radius: 94px;
    top: -90px;
    position: absolute;
    border: 8px solid #449dad;
    right: 0;
    left: 0;
    width: 70%;
    margin: auto;
}

.btn.btnCancel {
    background: #201a4b !important;
    color: #fff;
    box-shadow: none;
    border: none;
    text-transform: none;
    padding: 0.375rem 1rem;
}
.loginForm-1{
  height: 300px;
  width: 70%;
}

.loginForm{
  display:flex;
  align-items: center;
  height: 431px;
}

.researchImage-L img{
    width: 100%;
}
.researchImage-L{
    width: 350px;
    overflow: hidden;
    height: 431px;}

input:-internal-autofill-selected {
    -webkit-appearance: menulist-button;
    background-color: none !important;}

.form-control{border-radius: 3rem;}

.forgot-text{text-align: right; font-size: 12px; margin-top: 8px; color: #9ea9aa;}